.btn__arrow {
  width: 60px;
  height: 30px;
  background-color: rgba(0, 0, 0, 0);
  border: rgba(0, 0, 0, 0);
  justify-content: center;
  align-items: center;
  margin-top: auto;
  display: flex;
}

.arrow__stem {
  width: 40px;
  height: 2px;
  background-color: #131313;
  transition: transform .25s ease-in-out, height .25s ease-in-out;
  position: relative;
  transform: scaleX(100%);
}

.arrow__tip {
  width: 10px;
  height: 10px;
  border-bottom: 2px solid #131313;
  border-right: 2px solid #131313;
  border-radius: 0 2px 1px;
  transition: border-right .25s ease-in-out, border-bottom .25s ease-in-out, width .25s ease-in-out, height .25s ease-in-out, top .25s ease-in-out;
  position: absolute;
  top: -4px;
  right: 0;
  transform: rotate(-45deg);
}

.btn__arrow:hover .arrow__stem {
  transform: scaleX(120%);
}

.btn__arrow:focus {
  border: inherit;
  outline: inherit;
}

.btn__arrow:focus .arrow__stem {
  height: 3px;
  transform: scaleX(120%);
}

.btn__arrow:focus .arrow__tip {
  width: 16px;
  height: 16px;
  border-bottom: 3px solid #131313;
  border-right: 3px solid #131313;
  top: -6px;
}

